import Navigationn from "./componentt/Navigationn/Navigationn";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap";
import "./style.css";
import { useState, useEffect } from "react";
import Filterdata from "./componentt/Filterdata/Filterdata";

function App() {
  const [data, setData] = useState([]);
  const [search, setSearch] = useState("");
 
 
  const handleChange=(e)=>{
    setSearch(e.target.value);
   
  }

  const searchBtn = (e) => {
    getCertainData(search);
    e.preventDefault();
  };
  
  
  
  const getData = () => {
    fetch('http://localhost:8000/posts',{headers:{'content-type':'application/json','Accept':'application/json'}})
      .then((response) => {
        return response.json();
      })
      .then((posts)=>{
        setData(posts);

      })
     
  };
  useEffect(() => {
    getData();
  }, []);

  const getCertainData = (search) => {
    setData(data.filter((values)=>values.name.toLowerCase().includes(search.toLowerCase())));
  };
 
  return (

    <div className="App">
     
        <Navigationn
      handleChange={handleChange}
      searchBtn={searchBtn}/>

<Filterdata
data={data}
/>

    </div>
  );
}

export default App;
