import React from 'react';
import { useState} from "react";
import {Link} from "react-router-dom";

function Filterdata({data}) {
    
  const [currentPage, setCurrentPage] = useState([0]);
    const itemsPerPage=6;
    const startIndex=currentPage*itemsPerPage;
    const lastIndex=startIndex+itemsPerPage;
   const subset=data.slice(startIndex,lastIndex);
    const pageNumber =[];

    for(let i=0;i<=10;i++)
        {
            pageNumber.push(i);
    }

    const paginate = pageNumber => setCurrentPage(pageNumber);

  return (
    <div>
 <div className="caar">
    {subset.map((x)=>{
      return(
      
        <div className="container">
              
        <div className="card">
          <img
            
            src={x.imge}
            alt=""
          />
          <div className=" details">
            <h3 className="name">{x.name}</h3>
            <span className="year">{x.year}</span>
          </div>
          <div className="feature">
            <div className="icon1">
            <i class="bi bi-people">{x.capacity}</i>
            <i class="bi bi-fuel-pump">{x.fuel}</i>
            </div>
            <div  className="icon2">
            <i class="bi bi-speedometer">{x.speed}</i>
            <i class="bi bi-backpack3">{x.transmission}</i>
            </div>
          </div>
          <hr></hr>
          <div className="rent-details">
            <p>{x.prize}</p>
           <div className="heart">
            <i  class="bi bi-heart"></i>
            </div>
            <button>Rent Now</button>
           
          </div>
        </div>
       
      
       
      
      </div>
      
      )
        })}


</div>
    <nav>
        <ul className='pagination'>
            {pageNumber.map(number=>(
                <li key={number} className='page-item'>
<Link onClick={()=>paginate(number)} to={`page/${number}`}  className='page-link'>
{number}
</Link>
                </li>))}
           
        </ul>
        </nav>
        </div>
  )
}

export default Filterdata;